import { Box, Button, IconButton, Typography, useTheme, Table, TextField } from "@mui/material";
import { tokens, themeSettings } from "../../theme";
import { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";

import { AuthContext } from '../../context/login';

import config from "../../config";

// import { screen1 } from "../../data/mockData";

const Login = () => {
  const theme = useTheme();
  const navigate = useNavigate();
  const colors = tokens(theme.palette.mode);

  const auth = useContext(AuthContext);

  const [state, setState] = useState(0)

  const handleLogin = (e) => {
    e.preventDefault(); 

    const children = e.target.children;

    const user = children[2].value;
    const pass = children[6].value;

    console.log(user, pass)
    console.log(config.baseurl)
    fetch(
      `${config.baseurl}/login`,
      {
        method: "POST",
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          user: user,
          pass: pass,
        })
      }
    ).then(response => {
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      return response.json(); // Parse the response JSON
    })
    .then(data => {
      // Handle the response data here
      console.log(data);
      if (data.success) {
        auth.setToken(data.token)
        navigate("/");
      } else {
        alert("Invalid login.")
      }
    })
    .catch(error => {
      // Handle any errors that occurred during the fetch
      alert('Fetch error:', error);
    });
  }

  const handleLoginUser = (e) => {
    e.preventDefault(); 

    const children = e.target.children;

    const name = children[2].value;
    const code = children[6].value;

     fetch(
      `${config.baseurl}/getcode`,
      {
        method: "POST",
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: name,
          code: code,
        })
      }
    ).then(response => {
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      return response.json(); // Parse the response JSON
    })
    .then(data => {
      // Handle the response data here
      console.log(data);
      if (data.success) {
        auth.setToken({name: name, code: code});
        navigate("/awnser");
      } else {
        alert(data.message);
      }
    })
    .catch(error => {
      // Handle any errors that occurred during the fetch
      alert('Fetch error:', error);
    }); 
  }

  return (
    <Box
      width="100%"
      boxSizing="border-box"
      p="6%"
      height="calc(100% - 55px)"
    >
      <Typography
        variant="h2"
        fontWeight="500"
        color={colors.grey[100]}
      >
         KreQuiz Login
      </Typography>
      {state === 0 ? (
        <>
          <button className="login-buttons" onClick={(e) => {setState(1)}}>Underviser</button>
          <br />
          <button className="login-buttons" onClick={(e) => {setState(2)}}>Elev</button>
        </>
      ) : state === 1 ? (
        <>
          <form onSubmit={handleLogin}>
            <Typography
              variant="p"
              fontWeight="500"
              color={colors.grey[100]}
            >
               Brugernavn
            </Typography>
            <br />
            <input type="text" name="username" placeholder="Brugernavn" />
            <br />
            <Typography
              variant="p"
              fontWeight="500"
              color={colors.grey[100]}
            >
               Adgangskode
            </Typography>
            <br />
            <input type="password" name="password" placeholder="Adgangskode" />
            <br />
            <button className="login-buttons" type="submit">Logind</button>
          </form>
        </>
      ) : (
        <>
          <form onSubmit={handleLoginUser}>
            <Typography
              variant="p"
              fontWeight="500"
              color={colors.grey[100]}
            >
               Navn
            </Typography>
            <br />
            <input type="text" name="username" placeholder="Navn" />
            <br />
            <Typography
              variant="p"
              fontWeight="500"
              color={colors.grey[100]}
            >
               Kode
            </Typography>
            <br />
            <input type="text" name="code" placeholder="Kode" />
            <br />
            <button className="login-buttons" type="submit">Go!</button>
          </form>
        </>
      )}
    </Box>
  )
}

export default Login;
