import { Box, Button, IconButton, Typography, useTheme, Table, TextField } from "@mui/material";
import { tokens, themeSettings } from "../../theme";
import { useState, useEffect, useContext } from "react";
import { useParams } from 'react-router-dom';
import io from 'socket.io-client';
import { useNavigate } from "react-router-dom";

import { AuthContext } from '../../context/login';

import Progress from '../../componentes/progress/progress';

import config from "../../config";

const Quiz = () => {
  const params = useParams();
  const navigate = useNavigate();

  const theme = useTheme();
  const colors = tokens(theme.palette.mode);

  const auth = useContext(AuthContext);

  const [code, setCode] = useState("...");

  const [questions, setQuestions] = useState("...");
  const [options, setOptions] = useState([]);
  const [awnsers, setAwnsers] = useState({});

  const [activeQ, setActiveQ] = useState(-1);
  const [users, setUsers] = useState([]);
  const [isShowResult, setIsShowResult] = useState(false);

  const [userSocket, setUserSocket] = useState(null);

  useEffect(() => {
    const socket = io(config.baseurl); // Replace with your Flask server URL

    socket.on('connect', () => {
      console.log('Connected to WebSocket');
      setUserSocket(socket);
    });

    socket.on('disconnect', () => {
      console.log('Disconnected from WebSocket');
      setUserSocket(null);
    });

    // You can define custom events and handlers here
    // For example:
    socket.on('message', (data) => {
      console.log('Received message:', data);
    });

    socket.on('server_code', (data) => {
      console.log('Received code:', data);
      setCode(data.code);
      setQuestions(data.questions)
    });

    socket.on('server_join', (data) => {
      console.log('Received code:', data);
      setUsers((prevUsers) => [...prevUsers, data.name]);
    });

    socket.on('server_awnsers', (data) => {
      console.log('Received message:', data);
      setOptions(data.options);
      setAwnsers(data.awnsers);
    })

    socket.emit('start_quiz', {
      token: auth.token,
      id: params.id,
    });

    return () => {
      socket.disconnect();
      setUserSocket(null);
    };
  }, []);

  const next_question = () => {
    setActiveQ(activeQ + 1);
    setIsShowResult(false);
    console.log(activeQ)
  }

  useEffect(() => {
    if (userSocket !== null && activeQ !== questions.length) {
      console.log(questions, activeQ)
      userSocket.emit("next_question", {qid: questions[activeQ][0], code: code})
    }
  }, [activeQ])

  const show_result = () => {
    if (userSocket !== null) {
      console.log("SHOWING RESULT")
      userSocket.emit("get_awnsers", {qid: questions[activeQ][0], code: code})
      setIsShowResult(true);
    }
  }

  return (
    <Box
      width="100%"
      boxSizing="border-box"
      p="6%"
      height="calc(100% - 55px)"
    >
      <Typography
        variant="h2"
        fontWeight="500"
        color={colors.grey[100]}
      >
         {`KreQuiz - ${code}`}
      </Typography>
     
      {activeQ === questions.length ? (
        <>
          <Progress users={awnsers} qAmount={questions.length} />
          <button className="bottom-buttons" onClick={() => navigate("/")}>{"Afslut Quiz"}</button>
        </>
      ) : (
        <>
          {
            isShowResult === true ? (
              <>
                <Typography
                  variant="h3"
                  fontWeight="500"
                  color={colors.grey[100]}
                >
                   {questions[activeQ][1]}
                </Typography>
                <br />
                <Box
                  display="flex"
                  flexWrap="wrap"
                  justifyContent="space-between"
                  width="100%"
                >
                  {options.map((item) => {return (
                    <button 
                      className="show-input-buttons"
                      style={{ backgroundColor: item[2] == 0 ? colors.redAccent[800] : colors.greenAccent[800] }}
                    >
                      {item[1]}
                    </button>
                  )})}
                </Box>

                <Progress users={awnsers} qAmount={questions.length} />

                <button className="bottom-buttons" onClick={next_question}>{">>>>"}</button>
              </>
            ) : (
              <>
                {activeQ === -1 ? (
                  <>
                    <Box>
                      {users.map((item) => {return (
                        <Box key={item}>
                          <Typography
                            variant="h4"
                            fontWeight="500"
                            color={colors.grey[100]}
                          >
                            {item}
                          </Typography>
                        </Box>
                      )})}
                    </Box>
                    <button className="bottom-buttons" onClick={next_question}>{"Start!"}</button>
                  </>
                ) : (
                  <>
                    <Typography
                      variant="h3"
                      fontWeight="500"
                      color={colors.grey[100]}
                    >
                      {questions[activeQ][1]}
                    </Typography>
      
                    <Box
                      display="flex"
                      justifyContent="center"
                      alignItems="center"
                      pt={2}
                    >
                      <img 
                        src={`${config.baseurl}/static/img/${questions[activeQ][2]}`} 
                        alt=""
                        style={{ height: "60vh" }}
                      />
                    </Box>

                    <button className="bottom-buttons" onClick={show_result}>{"Se resluater"}</button>
                  </>
                )}
              </>
            )
          }
        </>
      )}
    </Box>
  )
}

export default Quiz;
